"use client";
import React, { useState, useEffect } from "react";

const SIX_HOURS = 6 * 60 * 60 * 1000;
const THIRTY_MINUTES = 30 * 60 * 1000;

const initialTasks = [
  {
    id: 1,
    title: "Collect 10 plastic bottles",
    description: "Find and collect 10 bottles from your area.",
    images: [],
    lastCompleted: null,
    startedAt: null,
  },
  {
    id: 2,
    title: "Clean a park bench",
    description: "Wipe down and clean a park bench thoroughly.",
    images: [],
    lastCompleted: null,
    startedAt: null,
  },
  {
    id: 3,
    title: "Pick up 15 pieces of trash",
    description: "Collect 15 random pieces of trash.",
    images: [],
    lastCompleted: null,
    startedAt: null,
  },
];

export default function TasksPage() {
  const [tasks, setTasks] = useState([]);
  const [activeTaskId, setActiveTaskId] = useState(null);

  // Load persisted data
  useEffect(() => {
    const saved = JSON.parse(localStorage.getItem("tasks_v3"));
    const active = JSON.parse(localStorage.getItem("active_task"));

    if (saved) setTasks(saved);
    else {
      setTasks(initialTasks);
      localStorage.setItem("tasks_v3", JSON.stringify(initialTasks));
    }

    if (active) setActiveTaskId(active);
  }, []);

  useEffect(() => {
    if (tasks.length > 0)
      localStorage.setItem("tasks_v3", JSON.stringify(tasks));

    localStorage.setItem("active_task", JSON.stringify(activeTaskId));
  }, [tasks, activeTaskId]);

  /* ========================= HELPERS ========================= */

  const isCooldown = (task) => {
    if (!task.lastCompleted) return false;
    return Date.now() - task.lastCompleted < SIX_HOURS;
  };

  const getCooldownTime = (task) => {
    const remaining = SIX_HOURS - (Date.now() - task.lastCompleted);
    const h = Math.floor(remaining / 3600000);
    const m = Math.floor((remaining % 3600000) / 60000);
    return `${h}h ${m}m`;
  };

  const getFinishUnlockTime = (task) => {
    if (!task.startedAt) return null;
    const remaining = THIRTY_MINUTES - (Date.now() - task.startedAt);
    const m = Math.max(0, Math.floor(remaining / 60000));
    const s = Math.max(0, Math.floor((remaining % 60000) / 1000));
    return `${m}m ${s}s`;
  };

  const startTask = (taskId) => {
    if (activeTaskId) return; // only allow one active
    setActiveTaskId(taskId);

    setTasks((prev) =>
      prev.map((t) => (t.id === taskId ? { ...t, startedAt: Date.now() } : t))
    );
  };

  const uploadImage = (taskId, file) => {
    const reader = new FileReader();
    reader.onloadend = () => {
      setTasks((prev) =>
        prev.map((t) =>
          t.id === taskId ? { ...t, images: [...t.images, reader.result] } : t
        )
      );
    };
    reader.readAsDataURL(file);
  };

  const finishTask = (taskId) => {
    setTasks((prev) =>
      prev.map((t) =>
        t.id === taskId
          ? {
              ...t,
              lastCompleted: Date.now(),
              startedAt: null,
              images: [],
            }
          : t
      )
    );
    setActiveTaskId(null);
  };

  /* ========================= UI ========================= */

  return (
    <div className="min-h-screen bg-[#050505] px-6 py-16 text-white">
      <h1 className="text-5xl font-extrabold text-center mb-14 text-green-500 drop-shadow-[0_0_15px_#00ff91]">
        Daily CleanTM Tasks
      </h1>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-12 max-w-7xl mx-auto">
        {tasks.map((task) => {
          const cooldown = isCooldown(task);
          const isActive = activeTaskId === task.id;
          const someTaskActive = activeTaskId && !isActive;
          const canFinish =
            isActive && Date.now() - task.startedAt >= THIRTY_MINUTES;

          return (
            <div
              key={task.id}
              className="
                rounded-3xl p-6 bg-[#0a0a0a] border border-green-700/40 
                shadow-[0_0_25px_5px_rgba(0,255,150,0.08)]
                hover:shadow-[0_0_35px_10px_rgba(0,255,150,0.15)]
                transition-all duration-500 flex flex-col justify-between
              "
            >
              <div>
                <h2 className="text-2xl font-bold text-green-400 drop-shadow-sm">
                  {task.title}
                </h2>
                <p className="text-gray-300 mt-3">{task.description}</p>

                {/* IMAGES */}
                {task.images.length > 0 && (
                  <div className="flex flex-wrap mt-4 gap-2">
                    {task.images.map((img, i) => (
                      <img
                        key={i}
                        src={img}
                        className="h-20 w-20 rounded-xl border border-green-600 object-cover"
                      />
                    ))}
                  </div>
                )}
              </div>

              {/* STATUS */}
              <div className="mt-6">
                {cooldown ? (
                  <p className="text-red-400 font-bold text-center">
                    Refreshes in {getCooldownTime(task)}
                  </p>
                ) : isActive ? (
                  <p className="text-yellow-400 font-semibold text-center">
                    Finish unlocked in {getFinishUnlockTime(task)}
                  </p>
                ) : someTaskActive ? (
                  <p className="text-gray-400 text-center text-sm">
                    Another task is in progress...
                  </p>
                ) : null}
              </div>

              {/* UPLOAD */}
              {!cooldown && isActive && (
                <div className="mt-4">
                  <input
                    type="file"
                    onChange={(e) => uploadImage(task.id, e.target.files[0])}
                    className="block w-full text-sm text-green-200 file:bg-green-600 file:text-white file:px-4 file:py-2 file:rounded-full cursor-pointer"
                  />
                </div>
              )}

              {/* BUTTONS */}
              <div className="mt-6">
                {!cooldown && !isActive && !someTaskActive && (
                  <button
                    onClick={() => startTask(task.id)}
                    className="
                      w-full bg-green-600 hover:bg-green-700 
                      text-white py-2.5 rounded-full font-bold 
                      transition shadow-lg
                    "
                  >
                    Start Task
                  </button>
                )}

                {isActive && (
                  <button
                    disabled={!canFinish}
                    onClick={() => finishTask(task.id)}
                    className={`
                      w-full py-2.5 rounded-full font-bold transition shadow-lg
                      ${
                        canFinish
                          ? "bg-green-500 hover:bg-green-600"
                          : "bg-gray-600 cursor-not-allowed"
                      }
                    `}
                  >
                    Finish Task
                  </button>
                )}
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
}
